.mask{
    height: 100%;
    width: 100%;
    position: relative;
}


.intro-img{
height: 100%;
width: 100%;
background-size: cover;
background-position: center;
object-fit: cover;
}

.mask::after{
    content:"";
    width: 100%;
    height: 100%;
     position: absolute;
     top: 0;
     left: 0;
     background-color: #000;
     opacity: 0.8;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top:50%;
    left: 50%;
    text-align: center;
}

.hero .content h1{
    font-size: 4rem;
    padding: 0.4rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase ;
}

.content .btn{
    margin: 1.2rem 0.3rem;
}

@media  screen and (max-width:640px) {
    .hero .content {
        display: relative;
        top: 20%;
        }
    .hero .content h1{
        font-size: 2.5rem;
    }
    .hero .content p{
        font-size: 1.4rem;
    }
}